import React from 'react';
import Helmet from 'react-helmet';
import '../../../Components/Pages/Tutorials.css';
import { BlockMath } from 'react-katex';
import Inline from '../../../Components/Latex/Inline';
import Latex from '../../../Components/Latex/latex';
import '../../../../node_modules/katex/dist/katex.css';
import Image from '../../../Components/Image/Image';
import Disqus from 'disqus-react';
const KalmanSmoothing = (props) => {
const disqusShortname = 'marofe-github-io';
const disqusConfig = {
        url: 'https://marofe.github.io/?p='+props.note.link,
        identifier: 'note-'+props.note.link,
        title: props.title
    };
return <article>
      <Helmet>
        <title>{props.note.title} | Marofe</title>
        <meta name="description" content={props.note.desc} />
    </Helmet>
    <h1>{props.title}</h1>
<p>{props.desc}</p>
    <p align="right">Last Update:  September 19, 2021.</p>
<div>
<h2>Introduction</h2>

Sometimes it is also of interest to estimate states for each time step conditional on all the measurements that we have obtained. This problem can be solved with Bayesian smoothing. In this note, the Bayesian theory of smoothing is presented. After that, the Rauch-Tung-Striebel (RTS) smoother, which is the closed form smoothing solution to linear Gaussian models, is derived.
<h2>Smoother</h2>

The purpose of Bayesian smoothing is to compute the marginal posterior distribution of the state <Inline math="x_k"/> at the time step <Inline math="k"/> after receiving a set of measurements up to a time step <Inline math="T"/>, where <Inline math="T> k"/>. This pdf is denoted by <Inline math="p(x_k|y_{1:T})"/>. 
<p></p>
The difference between filters and smoother is that the Bayesian filter computes its estimates using only the measurements obtained before and at the time step <Inline math="k"/>, but the Bayesian smoother uses also the future measurements for computing its estimates <a href="#cite.sarkka2013">sarkka2013</a>.
<div class='theorem'><b>Theorem:</b><br/>
The backward recursive equations for computing the smoothed distributions <Inline math="p(x_k|y_{1:T})"/> for any <Inline math="k<T"/> are 
<Latex math="\begin{aligned}
p(x_k|y_{1:T})&=p(x_{k}|y_{1:k})\int \frac{p(x_{k+1}|x_{k})p(x_{k+1}|y_{1:T})}{p(x_{k+1}|y_{1:k})}dx_{k+1}
\end{aligned}"/>
where <Inline math="p(x_k|y_{1:k})"/> is the filtering distribution and <Inline math="p(x_{k+1}|y_{1:k})"/> is the predictive distribution one-step ahead, given by the Chapman-Komogorov Equation,
<Latex math="\begin{aligned}
    p(x_{k+1}|y_{1:k})=\int p(x_{k+1}|x_k)p(x_k|y_{1:k})dx_k.
\end{aligned}"/>
</div>
<div class='proof'><b>Proof:</b><br/>
Note that
<Latex math="\begin{aligned}
    p(x_k,x_{k+1}|y_{1:T})=p(x_k|x_{k+1},y_{1:T})p(x_{k+1}|y_{1:T})
\end{aligned}"/>
Due to the Markov properties of the state <Inline math="x_k"/>, one has <Inline math="p(x_k|x_{k+1},y_{1:T})=p(x_k|x_{k+1},y_{1:k})"/> and <Inline math="p(x_{k+1}|x_k,y_{1:k})=p(x_{k+1}|x_k)"/>, thus,
<Latex math="\begin{aligned}
    p(x_k,x_{k+1}|y_{1:T})=p(x_k|x_{k+1},y_{1:k})p(x_{k+1}|y_{1:T})\\
    =\frac{p(x_{k+1}|x_k)p(x_k|y_{1:k})}{p(x_{k+1}|y_{1:k})}p(x_{k+1}|y_{1:T})
\end{aligned}"/>
As <Inline math="p(x_k|y_{1:T})=\int p(x_k,x_{k+1}|y_{1:T})dx_{k+1}"/>, one concludes
<Latex math="\begin{aligned}
    p(x_k|y_{1:T})=\int\frac{p(x_{k+1}|x_k)p(x_k|y_{1:k})}{p(x_{k+1}|y_{1:k})}p(x_{k+1}|y_{1:T})dx_{k+1}\\
    =p(x_k|y_{1:k})\int\frac{p(x_{k+1}|x_k)p(x_{k+1}|y_{1:T})}{p(x_{k+1}|y_{1:k})}dx_{k+1}.
\end{aligned}"/>
<p align="right"><Inline math="\blacksquare"/></p></div>
Note that by starting at <Inline math="k=T-1"/>, one has
<Latex math="\begin{aligned}
    p(x_{T-1}|y_{1:T})=p(x_{T-1}|y_{1:T-1})\int \frac{p(x_T|x_{T-1})p(x_T|y_{1:T})}{p(x_T|y_{1:T-1})}dx_T
\end{aligned}"/>
Therefore, given <Inline math="p(x_T|y_{1:T})"/> (last filtering distribution), one can solve the smoothing problem for all <Inline math="k=T-1,T-2,\ldots,1"/> running backward in time.
<h2>RTS</h2>

The Rauch-Tung-Striebel (RTS) smoother, which is also called the Kalman smoother, can be used for computing the closed form smoothing solution for linear and Gaussian systems.
<div class='theorem'><b>Theorem:</b><br/>
The backward recursion equations for the (fixed interval) Rauch-Tung-Striebel smoother for a linear and Gaussian system are given as,
<Latex math="\begin{aligned}
    \hat{x}_{k+1|k}&=A\hat{x}_{k|k}\\
    P_{k+1|k}&=AP_{k|k}A^T+Q_k\\
    G_k&=P_{k|k}A^T P_{k+1|k}^{-1}\\
    \hat{x}_k^s&=\hat{x}_{k|k}+G_k(\hat{x}_{k+1}^s-\hat{x}_{k+1|k})\\
    P_{k}^s&=P_{k|k}-G_k(P_{k+1}^s-P_{k+1|k})G_k^T
\end{aligned}"/>
where <Inline math="\{\hat{x}_{k|k},P_{k|k}\}"/> are the mean and covariance computed by the Kalman Filter.
</div>
<div class='proof'><b>Proof:</b><br/>
First, consider the following auxiliary lemma,
<div class='lemma'><b>Lemma:</b><br/>
<a class="label" id="lemma:aux1"></a>
If two random variables <Inline math="x\in\mathbb{R}^n"/> and <Inline math="y\in\mathbb{R}^m"/> have Gaussian distributions in the form
<Latex math="\begin{aligned}
    x&\sim \mathcal{N}(\mu_x,P_x)\\
    y|x&\sim\mathcal{N}(Hx,P_{y|x})
\end{aligned}"/>
Thus, the joint distribution of <Inline math="x"/> and <Inline math="y"/> is given by
<Latex math="\begin{aligned}
    p(x,y)=\mathcal{N}\left(
    \begin{bmatrix}
    \mu_x\\
    H\mu_x
    \end{bmatrix};\begin{bmatrix}
    P_x & P_xH^T \\
    HP_x & P_{y|x}+HP_xH^T 
    \end{bmatrix}
    \right)
\end{aligned}"/>
</div>
<div class='proof'><b>Proof:</b><br/>
See <a href="#cite.sarkka2013">sarkka2013</a>.
<p align="right"><Inline math="\blacksquare"/></p></div>
Accordingly, one can state,
<Latex math="\begin{aligned}
    x_{k}|y_{1:k}&\sim\mathcal{N}(\hat{x}_{k|k},P_{k|k})\\
    x_{k+1}|x_{k}&\sim \mathcal{N}(Ax_k,Q_k)
\end{aligned}"/>
and from <a href="#emma:aux1">lemma:aux1</a> one has
<Latex math="\begin{aligned}
    p(x_k,x_{k+1}|y_{1:k})=\mathcal{N}\left(
    \begin{bmatrix}
    \hat{x}_{k|k}\\
    A\hat{x}_{k|k}
    \end{bmatrix};\begin{bmatrix}
    P_{k|k} & P_{k|k}A^T \\
    AP_{k|k} & AP_{k|k}A^T +Q_k
    \end{bmatrix}
    \right).
\end{aligned}" label="eq:joint.predictive.dist"/>
Now, consider a second auxiliary lemma.
<div class='lemma'><b>Lemma:</b><br/>
<a class="label" id="lemma:aux2"></a>
If two random variables have joint Gaussian distribution in the form
<Latex math="\begin{aligned}
    p(x,y)=\mathcal{N}\left(
    \begin{bmatrix}
    \mu_x\\
    \mu_y
    \end{bmatrix};\begin{bmatrix}
    P_x & P_{xy} \\
    P_{yx} & P_y
    \end{bmatrix}
    \right)
\end{aligned}"/>
Thus,
<Latex math="\begin{aligned}
    p(x|y)&=\mathcal{N}(\mu_x+P_{xy}P_y^{-1}(y-\mu_y),P_x-P_{xy}P_y^{-1}P_{yx})\\
    p(y|x)&=\mathcal{N}(\mu_y+P_{yx}P_x^{-1}(x-\mu_x),P_y-P_{yx}P_x^{-1}P_{xy})
\end{aligned}"/>
</div>
Therefore, from <a href="#q:joint.predictive.dist">eq:joint.predictive.dist</a>, one has
<Latex math="\begin{aligned}
    p(x_k|x_{k+1},y_{1:k})=\mathcal{N}(\hat{x}_{k|k}+G_k(x_{k+1}-\hat{x}_{k+1|k}),P_{k|k}-G_kP_{k+1|k}G_k^T)
\end{aligned}"/>
Note that we have
<Latex math="\begin{aligned}
    x_{k+1}|y_{1:T}&\sim\mathcal{N}(\hat{x}_{k+1}^s,P_{k+1}^s)\\
    x_k|x_{k+1},y_{1:k}&\sim\mathcal{N}(\hat{x}_{k|k}+G_k(x_{k+1}-\hat{x}_{k+1|k}),P_{k|k}-G_kP_{k+1|k}G_k^T)
\end{aligned}"/>
Therefore, using again <a href="#emma:aux1">lemma:aux1</a>, one gets
<Latex math="\begin{aligned}
 p(x_k,x_{k+1}|y_{1:T})=   \mathcal{N}\left(
    \begin{bmatrix}
    \hat{x}_{k+1}^s\\
    \hat{x}_{k}^s
    \end{bmatrix};\begin{bmatrix}
    P_{k+1}^s & P_{k+1}^sG^T \\
    GP_{k+1}^s & P_{k}^s
    \end{bmatrix}
    \right)
\end{aligned}"/>
Finally, taking <Inline math="p(x_k|y_{1:T})=\int p(x_k,x_{k+1}|y_{1:T})dx_{k+1}"/> one conclude that <Inline math="p(x_k|y_{1:T})=\mathcal{N}(\hat{x}^s,P_{k}^s)"/> where <Inline math="\hat{x}_k^s=\hat{x}_{k|k}+G_k(\hat{x}_{k+1}^s-\hat{x}_{k+1|k})"/> and <Inline math="P_{k}^s=P_{k|k}-G_k(P_{k+1}^s-P_{k+1|k})G_k^T"/>.
<p align="right"><Inline math="\blacksquare"/></p></div>

                    </div>
                    <Disqus.DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
                    </article>
                    }
                    export default KalmanSmoothing;