import React from 'react';
import './Tutorials.css';
const TutorialList = (prop) => {
    return <div className="divPage">
    <div class='listCell'>
    <h2><a href="/tutorials/rastreamento_usando_visao_filtro_kalman">Rastreamento de objetos usando Viso Computacional e Filtro de Kalman</a></h2>
    <p>Este trabalho apresenta um algoritmo em tempo real para rastreamento de objetos
em viso computacional, usando o Filtro de Kalman como mecanismo de predio para situaes de ocluso e ou
contaminao da cena por rudo. O principal objetivo deste trabalho  de apresentar de forma didtica o
desenvolvimento de um algoritmo de rastreamento de objetos baseado em cor. O algoritmo apresentado faz o
rastreamento do maior objeto simtrico de uma cor pr-definida presente na cena.  apresentado em detalhes a
implementao da etapa de segmentao da imagem, e posteriormente  apresentado uma estratgia para tratar situaes
com dois objetos da mesma cor. Por fim  demonstrado o uso do Filtro de Kalmam.</p>
<p>ltima atualizao:  13 de Dezembro de 2015.</p>
</div>
</div>
}
export default TutorialList;